header{
  position: relative;
  height: 90vh;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 0;
  margin: 0 auto;
  background-color: skyblue;
  display: block;
}

nav{
  position: relative;
  top: 0;
  z-index: 9;
  width: 100%;
  z-index: 0;
  display: block;
  background-color: rgba(0, 0, 0, .8);
}

nav img{
  position: relative;
  width: 65px;
  height: 60px;
  margin: 10px;
}

nav img:hover{
  cursor: pointer;
}

nav span:nth-child(2) a{
  position: relative;
  font-size: 14pt;
  font-style: normal;
  margin-right: 7px;
  color: snow;
  font-family: Helvetica, "Trebuchet MS", Verdana, sans-serif;
}

nav span:nth-child(3){
  float: right;
  padding: 25px 15px;
}

nav span:nth-child(3) i{
  font-size: 17pt;
  margin-left: 7px;
  color: goldenrod;
}

nav span:nth-child(3) i:hover{
  cursor: pointer;
}

#preview,
#preview img{
  position: relative;
  height: 86vh;
  z-index: 0;
}

#preview img{
  object-fit: cover;
}

#preview a{
  opacity: .2;
  transition: opacity .7s;
}

#preview a:hover{
  opacity: 1;
}

#preview a span:nth-child(1){
  font-size: 40pt;
  color: white;
}

#preview ol li{
  width: 10px;
  height: 10px;
  border: 1px solid white;
  border-radius: 50%;
}

#collection{
  background-color: #fff;
  margin: 0 auto;
}

#collection h1,
#contact h1{
  background-color: rgb(134, 67, 97);
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  color: #fff;
}

#collectionList{
  position: relative;
  padding: 25px 0 0 0;
  left: 1%;
  right: 1%;
  max-width: 98%;
  box-shadow: 0px 0px 0px 1px lightgrey;
  background-color: rgba(240, 240, 240, .7);
}

#collectionList img,
#collectionList h4,
#collectionList label{
  pointer-events: none;
}

#collectionList h4{
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-size: 22pt;
  opacity: .9;
  margin-top: 3px;
} 

#collectionList label{
  font-family: arial;
  margin: 0 auto;
}

#collectionList .item:hover{
  cursor: pointer;
}

#contact{
  background: #fff;
}

#contact span:nth-child(2){
  position: relative;
  top: 50px;
  width: 2px;
  height: 17em;
  background:rgba(0, 0, 0, 1);
  margin-left: 5px;
  margin-right: 5px;
}

#contact span:nth-child(1) b{
  position: relative;
  top: 40%;
  font-size: 20pt;
  font-weight: lighter;
  vertical-align: middle;
  user-select: none;
}

#contact span:nth-child(3){
  position: relative;
  padding-left: 40px;
}

footer{
  position: relative;
  background: rgba(0, 0, 0, .8);
  line-height: 50px;
  color: #fff;
}

footer i{
  padding-left: 7px;
  font-size: 15pt;
  vertical-align: middle;
}

footer i:hover{
  cursor: pointer;
}